
.transaction form{
    max-width: 500px;
    min-width: 290px;
    width: 100%;
    /* margin: 15px 15px; */
}

.transaction form .row{
    width: 100%;
    margin: 15px 0;
}

.transaction form input{
    width:60%;
    min-height: 48px;
    padding: 0 0px;
}

.transaction form button{
    width: 200px;
    height: 40px;
    background: #555;
    color: white;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 700;
}